import { Bar, Chart } from '../Chart/Chart';

export * from './Barchart';
// import { Chart, Bar } from './Chart';

export const Barchart = ({ data }) => {
  // Width of each bar
  const itemWidth = 20;

  //   // Distance between each bar
  const itemMargin = 5;

  const dataLength = data.length;

  //   // Normalize data, we'll reduce all sizes to 25% of their original value
  const massagedData = data.map(datum => Object.assign({}, datum, { repos: datum.repos * 0.25 }));
  window.massagedData = massagedData;

  const mostRepos = massagedData.reduce((acc, cur) => {
    const { repos } = cur;
    return repos > acc ? repos : acc;
  }, 0);
  window.mostRepos = massagedData;

  const chartHeight = mostRepos;

  return (
    <div>
      <Chart width={dataLength * (itemWidth + itemMargin)} height={chartHeight}>
        {massagedData.map((datum, index) => (
          <Bar
            key={datum.name}
            x={index * (itemWidth + itemMargin)}
            y={0}
            width={itemWidth}
            height={datum.repos}
          />
        ))}
      </Chart>
    </div>
  );
};
